.fdd-input__caption {    
  display: inline-block;
  margin-bottom: 5px;
}

.fdd-input__input {
  border: 1px solid $toxin-color-black-25;
  border-radius: 4px;
  background: white;
  padding: 13px 15px;
  font-size: 14px;  
  height: 44px;
  width: 100%;
}

.fdd-input__input::placeholder {
  font-size: 14px;
  line-height: 18px;
  color: $toxin-color-black-25;  
}
.fdd-input__input:hover,
.fdd-input__input:focus {
  border: 1px solid $toxin-color-black-50;
}

.fdd-input__wrapper {    
  position: relative;  
}

.fdd-input__wrapper:after {
  content: "keyboard_arrow_down";
  font-family: 'Material Icons';      
  color: $toxin-color-black-50;    
  font-size: 24px;  
  position: absolute;
  top: 0;
  right: 0;    
  width: 44px;
  height: 44px;
  display: flex;
  justify-content: center;
  align-items: center;
  pointer-events: none;  
}